import React from "react";
import CreateIcon from "./CreateIcon";

function Card(props) {
  let pokemonID = props.id;
  if (pokemonID < 10) {
    pokemonID = "00" + props.id;
  } else if (pokemonID < 100) {
    pokemonID = "0" + props.id;
  }
  return (
    <div className="card-container">
      <h3>
        #{pokemonID} {props.name}
      </h3>
      <img className="pokemon-card-img" src={props.imgURL} alt="pokemon"></img>
      <div className="bottom-card-content">
        {props.type.map(CreateIcon)}
        <div className="card-stats">
          <span>HP: {props.base.HP}</span>
          <span>Attack: {props.base.Attack}</span>
          <span>Defense: {props.base.Defense}</span>
          <span>Speed: {props.base.Speed}</span>
        </div>
      </div>
    </div>
  );
}

export default Card;
