import React from "react";
import Header from "./Header";
import Card from "./Card";
import pokemon from "../pokemon";

function createCard(pokemon) {
  return (
    <Card
      key={pokemon.id}
      id={pokemon.id}
      name={pokemon.name.english}
      imgURL={pokemon.image}
      type={pokemon.type}
      base={pokemon.base}
    />
  );
}

function App() {
  return (
    <div>
      <Header />
      <section className="cards-section">{pokemon.map(createCard)}</section>
    </div>
  );
}

export default App;
